.gnm-skycam{
  padding-top: 4px;
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.42);
  border-radius: 3px;
  overflow: hidden;
}

.gnm-skycam .toolbar{
  padding:5px;
}

.gnm-skycam .toolbar h4{
  display: inline;
}

.gnm-skycam button{
  /* float: right; */
  padding:0 5px;
  margin:4px;
  border-radius: 3px;
  border: none;
  outline:none;
}
.gnm-skycam button .fa:before{
  cursor: pointer;
}

.gnm-skycam .skycam-container{
  margin-top:8px;
  width: 100%;
  /* min-height: 246px; */
}
@media (max-width:996px) {
  .gnm-skycam *{
    color: white;
  }
  .gnm-skycam .fa{
    color: black;
  }
  
}
