/*WX Takeover */

.gnm-weather-takeover {
  overflow: hidden;
  padding-top:10px;
  margin-bottom: 20px;
  background: #181818;
}


.gnm-weather-takeover * {
  font-family: 'Roboto';
}

.gnm-weather-takeover i {
  font-family: 'FontAwesome';
}

.gnm-weather-takeover button {
  outline: 0;
}

.gnm-weather-takeover h3 {
  font-size: 18px;
  font-weight: 500;
  color: #f9cf6c;
  margin-bottom: 0;
}

.gnm-weather-takeover .left_section {
  float: left;
  padding: 17px 14px 30px 29px;
}

.gnm-weather-takeover h2 {
  font-size: 48px;
  font-weight: normal;
  color: #fff;
  line-height: 1.1;
}

.gnm-weather-takeover .left_section .wxLivestream {
  border-radius: 2px;
  background: #272727;
  margin-top: 8px;
  padding: 10px;
}

.gnm-weather-takeover .right_section {
  width: 389px;
  height: 712px;
  float: right;
  padding: 0 14px 14px;
  background: #2f2f2f;
  position: relative;
}

.gnm-weather-takeover #wxLSPlayer {
  position: relative;
}

.gnm-weather-takeover #wxLSoptions {
  margin-top: 10px;
}

.gnm-weather-takeover #wxLSoptions h3 {
  margin-bottom: 10px;
}

.gnm-weather-takeover #wxLSoptions .row {
  height: 47px;
  margin: 4px 0 12px;
}

.gnm-weather-takeover #wxLSoptions .ls_option {
  margin-bottom: 10px;
  background: #353535;
  border: 1px solid #3a3a3a;
  height: 47px;
  display: table;
  position: relative;
  transition: all 0.25s linear;
  width: 100%;
}

.gnm-weather-takeover #wxLSoptions.amt3 .ls_option[data-ind="1"], .gnm-weather-takeover #wxLSoptions.amt3 .ls_option[data-ind="2"], .gnm-weather-takeover #wxLSoptions.amt4 .ls_option {
  width: 49.2%;
}

.gnm-weather-takeover #wxLSoptions.amt3 .ls_option[data-ind="1"], .gnm-weather-takeover #wxLSoptions.amt4 .ls_option[data-ind="0"], .gnm-weather-takeover #wxLSoptions.amt4 .ls_option[data-ind="2"] {
  float: left;
}

.gnm-weather-takeover #wxLSoptions.amt3 .ls_option[data-ind="2"], .gnm-weather-takeover #wxLSoptions.amt4 .ls_option[data-ind="1"], .gnm-weather-takeover #wxLSoptions.amt4 .ls_options[data-ind="3"] {
  float: right;
}

.gnm-weather-takeover #wxLSoptions .ls_option:hover {
  background: #171717;
}

.gnm-weather-takeover #wxLSoptions .ls_option a.ls_picker {
  height: 47px;
  width: 100%;
  display: table-cell;
  vertical-align: middle;
  font-size: 14px;
  color: #cdcdcd;
  border: 0;
  text-decoration: none;
  padding: 0 10px;
}

.gnm-weather-takeover #wxLSoptions .ls_option a.undock {
  display: block;
  position: absolute;
  right: 5px;
  bottom: 3px;
  color: #9e9e9e;
  font-size: 10px;
  text-decoration: none;
  transition: all 0.25s linear
}

.gnm-weather-takeover #wxLSoptions .ls_option a.undock i {
  font-size: 14px;
}

.gnm-weather-takeover #wxLSoptions .ls_option a.undock:hover {
  color: #fff;
}

.gnm-weather-takeover #wxLSoptions .ls_option .streaming {
  display: none;
  font-size: 11px;
  color: #9e9e9e;
  font-style: italic;
  position: absolute;
  bottom: 3px;
  right: 7px;
}

.gnm-weather-takeover #wxLSoptions .ls_option.selected {
  border: 1px solid #b70000;
  background: #171717;
}

.gnm-weather-takeover #wxLSoptions .ls_option.selected a.ls_picker {
  color: #fff;
}

.gnm-weather-takeover #wxLSoptions .ls_option.selected .streaming {
  display: block;
}

.gnm-weather-takeover #wxLSoptions .one_item .ls_option {
  width: 600px;
}

.gnm-weather-takeover #wxLSoptions .two_item, .gnm-weather-takeover #wxLSoptions .one_item {
  height: 49px;
}

.gnm-weather-takeover #wxLSoptions .two_item .ls_option {
  width: 293px;
  float: left;
}

.gnm-weather-takeover #wxLSoptions .two_item .ls_option:nth-child(1) {
  margin-right: 12px;
}

.gnm-weather-takeover .inner_border {
  border: 1px solid #3a3a3a;
  border-radius: 2px;
}

.gnm-weather-takeover .inner {
  padding: 5px 12px 0;
}

.gnm-weather-takeover .upload, .gnm-weather-takeover .safety {
  margin-bottom: 0;
}

.gnm-weather-takeover .upload p {
  color: #fff;
  font-size: 13px;
  margin: 4px 0 0;
}

.gnm-weather-takeover .upload button {
  border: 0;
  background: #f9cf6c;
  color: #000;
  width: 130px;
  height: 30px;
  border-radius: 2px;
  float: right;
  margin-top: 6px;
  cursor: pointer;
  transition: all 0.3s linear;
}

.gnm-weather-takeover .upload button:hover {
  background: #f7d688;
}

.gnm-weather-takeover .safety p {
  font-size: 11px;
  color: #fff;
  margin: 2px 0 0 10px;
}

.gnm-weather-takeover .social button {
  width: 100%;
  height: 30px;
  border: 1px solid #3a3a3a;
  border-bottom: 0px;
  border-left: 0;
  float: left;
  background: #181818;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s linear;
}

/*.gnm-weather-takeover .bottom .social button:hover{background:#f9cf6c;color:#181818;}*/

.gnm-weather-takeover .social #shareEmail {
  border-right: 0;
}

.gnm-weather-takeover .social #shareFacebook:hover {
  background: #3b5998;
}

.gnm-weather-takeover .social #shareTwitter:hover {
  background: #55acee;
}

.gnm-weather-takeover .social #shareEmail:hover {
  background: #c4302b;
}

.gnm-weather-takeover .downloadapp {
  display: table;
}

.gnm-weather-takeover .downloadapp img, .gnm-weather-takeover .bottom .downloadapp span {
  display: table-cell;
  vertical-align: middle;
}

.gnm-weather-takeover .downloadapp img {
  margin-right: 12px;
}

.gnm-weather-takeover .downloadapp span {
  font-size: 16px;
}

.gnm-weather-takeover .downloadapp .first {
  color: #fff;
  padding-right: 9px;
}

.gnm-weather-takeover .downloadapp .second {
  color: #b8b8b8;
}

.gnm-weather-takeover .watcheswarnings .watchheader {
  position: relative;
}

.gnm-weather-takeover .watcheswarnings .watchheader p {
  color: #fff;
  font-size: 14px;
  margin: 1px 0 12px;
}

.gnm-weather-takeover #allWarnings {
  position: absolute;
  top: 5px;
  left: -53px;
  border: 0;
  background: #262626;
  width: 53px;
  height: 44px;
  border-radius: 2px 0 0 2px;
  color: #fff;
  font-size: 11px;
  font-weight: bold;
  cursor: pointer;
  box-shadow: -5px 1px 6px -2px #000;
}

.gnm-weather-takeover #allWarnings span {
  display: block;
}

.gnm-weather-takeover .warninglist {
  background: #3d3d3e;
  width: 100%;
  padding: 3px;
  position: relative;
}

.gnm-weather-takeover .warninglist ul {
  margin: 0;
  padding: 0;
}

.gnm-weather-takeover .warninglist ul li {
  list-style: none;
  height: 35px;
  line-height: 35px;
  background: #363636;
  padding: 0 9px;
}

.gnm-weather-takeover .warninglist ul li a {
  display: block;
  width: 100%;
  height: 100%;
}

.gnm-weather-takeover .warninglist ul li:nth-child(even) {
  background: #3d3d3e;
}

.gnm-weather-takeover .warninglist ul li i {
  font-size: 9px;
  margin-right: 6px;
}

.gnm-weather-takeover .warninglist ul li.tornado i {
  color: #ff0000;
}

.gnm-weather-takeover .warninglist ul li.tornado.watch i {
  color: #f6e635;
}

.gnm-weather-takeover .warninglist ul li.flood i {
  color: #00ff00;
}

.gnm-weather-takeover .warninglist ul li.thunderstorm i {
  color: #ffa500;
}

.gnm-weather-takeover .warninglist ul li.freezing.drizzle i {
  color: #00BFFF;
}

.gnm-weather-takeover .warninglist ul li.freezing.fog i {
  color: #008080;
}

.gnm-weather-takeover .warninglist ul li.freezing.rain i {
  color: #DA70D6;
}

.gnm-weather-takeover .warninglist ul li.heavy.snow i {
  color: #7B68EE;
}

.gnm-weather-takeover .warninglist ul li.ice.storm i {
  color: #8B008B;
}

.gnm-weather-takeover .warninglist ul li.sleet i {
  color: #6495ED;
}

.gnm-weather-takeover .warninglist ul li.snow.advisory i {
  color: #4169E1;
}

.gnm-weather-takeover .warninglist ul li.blowing.snow i {
  color: #1E90FF;
}

.gnm-weather-takeover .warninglist ul li.wind.chill.advisory i {
  color: #AFEEEE;
}

.gnm-weather-takeover .warninglist ul li.wind.chill.warning i {
  color: #B0C4DE;
}

.gnm-weather-takeover .warninglist ul li.winter.storm.warning i {
  color: #FF69B4;
}

.gnm-weather-takeover .warninglist ul li.winter.storm.watch i {
  color: #4682B4;
}

.gnm-weather-takeover .warninglist ul li.winter.weather.advisory i {
  color: #7B68EE;
}

.gnm-weather-takeover .warninglist ul li .county {
  color: #fff;
  font-size: 12px;
  font-weight: 500;
}

.gnm-weather-takeover .warninglist ul li .warning {
  color: #afafaf;
  float: right;
  font-size: 11px;
}

.gnm-weather-takeover .radars {
  margin-top: 22px;
}

.gnm-weather-takeover .radars h3 {
  margin-bottom: 6px;
}

.gnm-weather-takeover .radars .radar_inner {
  background: #3f3f3f;
}

.gnm-weather-takeover .radars .radar_inner img {
  width: 100%;
  display: block;
  margin-bottom: 9px;
}

.gnm-weather-takeover .radars .radar_inner button {
  width: 90%;
  height: 36px;
  border: 0;
  border-radius: 2px;
  background: #f9cf6c;
  color: #000;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s linear;
}

.gnm-weather-takeover .radars .radar_inner button:hover {
  background: #f7d688;
}

.gnm-weather-takeover .radars .radar_inner #warnRadar {
  float: right;
}

.gnm-weather-takeover .radars .radar_inner #streetRadar {
  float: left;
}

.gnm-weather-takeover .ad {
  padding-top: 13px;
  background: url(http://images.worldnow.com/images/static/container-e/ad43_header.gif) 0 0 no-repeat;
  width: 300px;
  height: 250px;
  margin: 18px;
}

.gnm-weather-takeover .col-xl-3 .ad {
  width: 300px;
  height: auto;
  margin: 18px auto;
}

.gnm-weather-takeover #hoverZone {
  cursor: pointer;
}

.gnm-weather-takeover #hoverZone .rightzone {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 100%;
  z-index: 5;
}

.gnm-weather-takeover #watchesAndWarnings {
  position: absolute;
  left: 100%;
  top: 0;
  transition: all 0.3s linear;
  height: auto;
  box-shadow: 1px 1px 7px 1px #000;
  background: #262626;
  width: 100%;
}

.gnm-weather-takeover #watchesAndWarnings:hover {
  left: 99%;
}

.gnm-weather-takeover #watchesAndWarnings h3 {
  margin-bottom: 10px;
}

.gnm-weather-takeover #watchesAndWarnings.expanded {
  left: 0;
}

.gnm-weather-takeover #watchesAndWarnings #closeWW {
  display: none;
  width: 53px;
  height: 44px;
  color: #fff;
  font-size: 11px;
  position: absolute;
  top: 15px;
  left: -53px;
  background: #262626;
  border: 0;
  border-radius: 2px 0 0 2px;
  cursor: pointer;
  font-weight: bold;
  box-shadow: -5px 1px 6px -2px #000;
}

.gnm-weather-takeover #watchesAndWarnings #closeWW i {
  display: block;
}

.gnm-weather-takeover #watchesAndWarnings.expanded #closeWW {
  display: block;
}

.gnm-weather-takeover #watchesAndWarnings.expanded #hoverZone {
  display: none;
}

.gnm-weather-takeover #watchesAndWarnings.scrollable .warninglist {
  height: 672px;
}

.gnm-weather-takeover #watchesAndWarnings.disable {
  display: none;
}

.gnm-weather-takeover #startGraphic {
  display: block;
}

.gnm-weather-takeover #startGraphic img {
  width: 100%;
}

.gnm-weather-takeover #startGraphic .blackhover {
  position: absolute;
  z-index: 2;
  background: #000;
  opacity: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.3s linear;
}

.gnm-weather-takeover #startGraphic i {
  position: absolute;
  font-size: 90px;
  color: #f9cf6c;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  text-shadow: -1px 1px 9px rgba(0, 0, 0, .7);
  transition: all 0.3s linear;
  z-index: 3;
}

.gnm-weather-takeover #startGraphic:hover i {
  color: #f7d688;
}

.gnm-weather-takeover #startGraphic:hover .blackhover {
  opacity: 0.4;
}

.gnm-weather-takeover #startGraphic.off {
  display: none;
}

.gnm-weather-takeover .farRight {
  width: 330px;
  background: #191919;
  height: 100%;
  padding: 12px 15px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 5;
}

.gnm-weather-takeover .watcheswarnings .downloadapp, .gnm-weather-takeover .watcheswarnings .safety {
  display: none;
}

.gnm-weather-takeover .watcheswarnings .downloadapp {
  padding-top: 5px;
  background: #3f3f3f;
  margin: 10px 0;
}

.gnm-weather-takeover .watcheswarnings .downloadapp>div {
  margin-bottom: 1rem;
}

.gnm-weather-takeover .watcheswarnings .downloadapp .col-xs-9 {
  padding-left: 0;
}

.gnm-weather-takeover .watcheswarnings .downloadapp .col-xs-9 h3 {
  font-size: 17px;
  margin-top: 0.5rem;
}

.gnm-weather-takeover .watcheswarnings .downloadapp .col-xs-9 p {
  color: #fff;
}

.gnm-weather-takeover .watcheswarnings .safety {
  background: #3f3f3f;
  margin: 0 0 10px;
}

.gnm-weather-takeover .watcheswarnings .safety a {
  text-decoration: none;
  outline: none;
  color: #fff;
  transition: all 0.3s linear;
  display: block;
}

.gnm-weather-takeover .watcheswarnings .safety a:hover {
  color: #f9cf6c;
}

.gnm-weather-takeover .watcheswarnings .safety button {
  background: none;
  border: 0;
  cursor: pointer;
  width: 100%;
}

.gnm-weather-takeover .watcheswarnings .safety a i {
  font-size: 23px;
}

.gnm-weather-takeover .watcheswarnings .safety .inner {
  margin-bottom: 5px;
}

.gnm-weather-takeover .watcheswarnings .safety .col-xs-12:first-child {
  margin-bottom: 1rem;
}

.gnm-weather-takeover .watcheswarnings .safety .col-xs-12:last-child {
  margin-bottom: 0;
}

.gnm-weather-takeover #mobileAlerts, .gnm-weather-takeover #mobileRadar, .gnm-weather-takeover #mobileTabs {
  display: none;
}

.gnm-weather-takeover>.col-xl-9.col-xs-12 {
  overflow: hidden;
  margin-bottom: 0;
}

.gnm-weather-takeover>.col-xl-9.col-xs-12 .col-xl-8.col-lg-8 {
  margin-bottom: 0;
}

.gnm-weather-takeover>.col-xl-9.col-xs-12 .col-xl-4.col-lg-4 {
  background: #2f2f2f;
  margin-bottom: 0;
}

.gnm-weather-takeover .radar_inner .col-xs-6, .gnm-weather-takeover .safety .col-xs-4 {
  margin-bottom: 1rem;
}

.gnm-weather-takeover .safety h3, .gnm-weather-takeover .upload .inner_border h3 {
  margin-top: 1rem;
}

.gnm-weather-takeover .safety .inner_border .col-xs-4 {
  margin-bottom: 0;
}

.gnm-weather-takeover .safety .inner_border .fb {
  padding-right: 0;
}

.gnm-weather-takeover .safety .inner_border .twit {
  padding: 0;
}

.gnm-weather-takeover .safety .inner_border .email {
  padding-left: 0;
}

.gnm-weather-takeover .safety .inner_border h3 {
  margin-left: 10px;
  margin-top: 1rem;
}

.gnm-weather-takeover .safety .inner_border .col-xs-12 {
  margin-bottom: 1rem;
}

.gnm-weather-takeover .safety .inner_border h3 {
  margin-left: 10px;
}

.gnm-weather-takeover .safety .inner_border p {
  margin-left: 10px;
}

.gnm-weather-takeover .upload .inner_border .col-xs-3 img {
  margin-left: 10px;
  margin-top: 5px;
}

.gnm-weather-takeover .upload .inner_border>.row>div, .gnm-weather-takeover .upload>.row>.col-xs-12, .gnm-weather-takeover .safety>.row>.col-xs-12 {
  margin-bottom: 1.5rem;
}

@media only screen and (min-width: 768px) {
  .gnm-weather-takeover>.col-xl-9.col-xs-12 .col-xl-4.col-lg-3 {
    background: none;
    border-left: 1px solid #2d2d2d;
  }
  .gnm-weather-takeover .left_section {
    width: 434px;
    padding: 17px 14px 30px 14px
  }
  .gnm-weather-takeover .left_section h2 {
    font-size: 34px;
  }
  .gnm-weather-takeover .right_section .ad, .gnm-weather-takeover .left_section .bottom, .gnm-weather-takeover #mobileAlerts, .gnm-weather-takeover #mobileRadar, .gnm-weather-takeover #mobileTabs {
    display: none;
  }
  .gnm-weather-takeover .left_section .wxLiveStream {
    padding: 0;
    background: none;
  }
  .gnm-weather-takeover .right_section .radars .radar_inner {
    width: auto;
    background: none;
  }
  .gnm-weather-takeover .right_section .radars .radar_inner img {
    width: 100%;
    margin-bottom: 15px;
  }
  .gnm-weather-takeover .right_section .radars #warnRadar {
    margin-right: 26px;
  }
  .gnm-weather-takeover #startGraphic i {
    font-size: 50px;
  }
  .gnm-weather-takeover .left_section .wxLivestream {
    background: none;
    padding: 0;
  }
}

@media only screen and (min-width: 992px) {
  .gnm-weather-takeover>.col-xl-9.col-xs-12 .col-xl-4.col-lg-3 {
    border: 0;
    background: #2f2f2f;
  }
  .gnm-weather-takeover #startGraphic i {
    font-size: 90px;
  }
}

@media (min-width: 1470px) {
  .gnm-weather-takeover .left_section .wxLivestream {
    width: 679px;
  }
  .gnm-weather-takeover .farRight, .gnm-weather-takeover .watcheswarnings .downloadapp, .gnm-weather-takeover .watcheswarnings .safety {
    display: block;
  }
  .gnm-weather-takeover .watcheswarnings .ad {
    display: none;
  }
  .gnm-weather-takeover .warninglist ul li {
    height: 41px;
    line-height: 41px;
  }
  .gnm-weather-takeover .warninglist ul li i {
    font-size: 13px;
  }
  .gnm-weather-takeover .warninglist ul li .county {
    font-size: 14.21px;
  }
  .gnm-weather-takeover .warninglist ul li .warning {
    font-size: 13.02px;
  }
  .gnm-weather-takeover h3 {
    font-size: 20px;
  }
}
