.gnm-react-temp-chart{
  width: 100%;
  background:white;
}

.gnm-react-temp-chart .temp-label{
  font-size: 12px;
}

.gnm-react-temp-chart .wind-label{
  font-size: 12px;
  position: relative;
  bottom:15px;
}

.gnm-react-temp-chart .titles span:first-child{
  margin-left: 50px;
}
.gnm-react-temp-chart .titles span:nth-child(2){
  float: right;
    margin-right: 24px;

}

.gnm-react-temp-chart .title{
  margin-left: 50px;
}
