/* This HAS to stay CSS-only for legacy support of consumer-affairs */

.gnm-current-conditions {
  width: 100%;
  height: 65px;
  position: relative;
  padding-top: 6px;
}

.gnm-current-conditions .condition-icon{
  width:35px;
  position: relative;
  top:8px;
  display: inline-block;
}
.gnm-current-conditions .map-link{
  padding-top: 5px;
  font-size:13px;
  color:white;
  display: block;
  text-align: right;
  color:white;

}
.gnm-current-conditions .radar-img > img{
  margin-top:4px;
  width: 100px;
  display: inline-block;
  position: relative;
  top:0px;
}
.gnm-current-conditions .radar-img {
  display: block;
}

.gnm-current-conditions .temperature{
    font-size: 30px;
    color:white;
    margin: 3px;
    position: relative;
    bottom:2px;
}

.gnm-current-conditions .feels-like{
  font-size: 12px;
  color: #cccccc;
  position: absolute;
  bottom: 0px;
  left:0px;
}

@media (min-width: 996px) and (max-width: 1199px) {
  .gnm-current-conditions {
    top:15px;
  }
}

@media (max-width:996px){
  .gnm-current-conditions{
    padding-top: 13px;
  }
}

@media (min-width: 1200px) {
  .gnm-current-conditions .feels-like{
    bottom: -24px;
    left: 2px;
  }
}

@media (max-width: 350px){
  .gnm-current-conditions .condition-icon{
    display: none;
  }
  .gnm-current-conditions .temperature{
    font-size: 24px;
  }
}
@media (max-width: 300px){
  .gnm-current-conditions{
    display: none;
  }
}
