/* This HAS to stay CSS-only for legacy support of consumer-affairs */
#gnm-main-body{
  transition: padding 500ms ease-in-out;
  padding-top: 101px;
  min-height: calc(100vh - 200px);
  width: 100% ;
  padding-top:178px; /* default for initial paint, gets written over */
}

@media (max-width:996px) {
  #gnm-main-body{
    background-color:#222222;
  }
}


.gnm-banner {
  background-color : #be0000;
  position: relative;
}

.gnm-banner .container {
  height : 10px;
}

.gnm-banner .show-all {
  z-index          : 19;
  position         : absolute;
  right            : 0px;
  width            : 40px;
  height           : 40px;
  padding          : 12px;
  color            : white;
  border           : 1px solid transparent;
  border-radius    : 0px;
  background-color : transparent;
  cursor           : pointer;
}

@media (min-width: 768px) {
  .gnm-banner .show-all {
    right : calc(100vw/2 - 750px/2 + 15px);
  }
}

@media (min-width: 992px) {
  .gnm-banner .show-all {
    right : calc(100vw/2 - 970px/2 + 15px);
  }
}

@media (min-width: 1200px) {
  .gnm-banner .show-all {
    right : calc(100vw/2 - 1170px/2 + 15px);
  }
}

@media (min-width: 1470px) {
  .gnm-banner .show-all {
    right : calc(100vw/2 - 1440px/2 + 15px);
  }
}

.gnm-banner .show-all .glyphicon {
  top : -1px;
}

.gnm-banner .show-all .glyphicon:hover:before {
  cursor : pointer;
}

.gnm-banner .show-all:focus,
.gnm-banner .show-all:hover {
  /* background-color: rgba(0,0,0,0.1); */
  outline-style : none;
}

.gnm-banner .glyphicon-chevron-up {
  transition : transform 600ms ease-in-out;
}

.gnm-banner .glyphicon-chevron-up.collapsed {
  transform : rotate3d(1,0,0,180deg);
}

.gnm-banner .notification {
  width            : 14px;
  height           : 14px;
  color            : white;
  border-radius    : 3px;
  background-color : black;
  font-size        : 12px;
}

/* START Production CSS */

.gnm-banner-control {
  transition : height 600ms ease-in-out;
}

.gnm-banner-control.active {
  height : 40px;
}

.gnm-banner-control.inactive {
  height : 0px;
}

.gnm-banner .item {
  position   : absolute;
  top        : 0px;
  left       : 0;
  width      : 100%;
  height     : 40px;
  max-height : 40px;
  overflow   : hidden;
}

.gnm-banner .alert-container {
  padding-left : 15px;
}

.gnm-banner .line-clamp {
  display     : -webkit-box;
  line-height : 28px;
  position: relative;
    bottom: 8px;
}

.gnm-banner .alert {

  display          : block;
  max-height       : 39px;
  margin-bottom: 0px;
  line-height: 12px;
  padding-left     : 15px;
  color            : #fff;
  border-radius    : 0;
  background-color : #333;
  font-weight      : 500;
  transition       : background-color 300ms ease-in-out;
}

@media (max-width: 768px) {
  .gnm-banner .alert.active {
    padding-right : 28px;
  }
}

@media (min-width: 768px) {
  .gnm-banner .alert {
    padding-left : calc(100vw/2 - 750px/2 + 5px);
  }
}

@media (min-width: 992px) {
  .gnm-banner .alert {
    padding-left : calc(100vw/2 - 970px/2 + 5px);
  }
}

@media (min-width: 1200px) {
  .gnm-banner .alert {
    padding-left : calc(100vw/2 - 1170px/2 + 5px);
  }
}

@media (min-width: 1470px) {
  .gnm-banner .alert {
    padding-left : calc(100vw/2 - 1440px/2 + 5px);
  }
}

@media (max-width:768px) {
  .gnm-banner .alert {
    font-size : 13px;
  }
}

.gnm-banner .alert-red {
  background-color : #be0000;
}

.gnm-banner .alert-dark-red {
  background-color : #800000;
}

.gnm-banner a {
  color       : #e5e5e5;
}
