.gnm-radar-call-to-action {
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.42);
  border-radius: 3px;
  overflow: hidden;
}

.gnm-radar-call-to-action h4{
  color: #333333;
  margin:0px;
  padding: 10px 7px 6px;
}

.gnm-radar-call-to-action img{
  /* min-height: 227px; */
  display: block;
  width: 100%;
}
.gnm-radar-call-to-action div{
  position:relative;
}

.gnm-radar-call-to-action div:before{
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 1;
  background :rgba(0, 0, 0, .05);
  transition : opacity 300ms ease-in-out;
  pointer-events: none;
}
.gnm-radar-call-to-action div:hover:before{
  opacity: 0;
}

@media (max-width:996px) {
  .gnm-radar-call-to-action h4{
    color:white;
    text-align: center;
  }
    .gnm-radar-call-to-action a{
      color:white;
      
    }
}
