
.gnm-weather-summary h3{
  font-weight:300px;
  font-size: 25px;
  color: #111111;
  margin-top:0px;
  margin-bottom:5px;

}

.gnm-weather-summary p{
  margin-top: 10px;
    margin-bottom: 15px;
}

.gnm-weather-summary a{
  font-weight: 400;
  color: #111111;
  text-decoration: none;
  border-bottom: 1px dashed #111111;
}

.gnm-weather-summary .icon-container{
  float:left;
  width: 135px;
  padding: 8px;
  position: relative;
}

.gnm-weather-summary .icon-container img{
  position: absolute;
  top: 39px;
    width: 40px;
    left: 20px;

}

.gnm-weather-summary .icon-container div{
  text-align: right;
  clear: both;
  position: relative;
  right:15px;
  bottom: -6px;
}

.gnm-weather-summary .temp-arc{
  width: 182px;
  height: 91px;
}

.gnm-weather-summary .icon-box{
  float:left;
  margin-top: 5px;
  margin-left: 30px;
}

.gnm-weather-summary .temperature{
  font-weight:500;
  font-size: 36px;
  color: #111111;
  margin-left: 7px;
}


.gnm-weather-summary .detail{
  display: inline-block;
  padding: 8px 4px;
  clear:left;
  text-align: center;
}

.gnm-weather-summary h2{
  font-weight: 400;
  font-size: 30px;
  color: #111111;
  line-height: 38px;
}

.gnm-weather-summary h4{
  font-weight: 400;
  font-size: 25px;
  color: #111111;
  line-height: 35px;
}

.gnm-weather-summary .progressbar-text:after{
  content:'\00b0';
}

.gnm-weather-summary span.temperature{
  position: absolute;
  left: 60px;
  top: 31px;
}
.gnm-weather-summary span.temperature:after{
  content:'\00b0';

}

.gnm-weather-summary-small{
  margin-left:5px !important;
  margin-right: 5px !important;
}

.gnm-weather-summary-small img{
  padding:10px;
  width: 50%;
  float:left;
  max-width: 100px;
}
.gnm-weather-summary-small h2{
  color:white;
  font-size: 68px;
  font-weight: 500;
  line-height: 103px;
  text-align: center;
  float:left;
}

.gnm-weather-summary-small h1{
  color:white;
  line-height: 28px;
  padding-bottom: 10px;
}

.gnm-weather-summary-small h3{
  color:white;
  line-height: 44px;
}
.gnm-weather-summary-small h4{
  display: inline;
  color: white;
  font-size: 21px;
  font-weight: 500;
}

.gnm-weather-summary-small span{
  color: #ccc;
  font-weight: 500;
}
.gnm-weather-summary-small p{
  color: white;
  font-weight: 500;
  clear: left;
}

.gnm-weather-summary-small a{
  color: white;
  text-decoration: none;
  border-bottom: 1px dashed white;
}
.gnm-weather-summary-small ul{
  list-style: none;
}
.gnm-weather-summary-small li{
  float:left;
  padding:2px 10px;
  width: 50%;
}
.gnm-weather-summary-small a.radar{
  border: none;
    border-radius: 5px;
    padding-top: 8px;
    padding-bottom: 8px;
    /* width: 100%; */
    font-weight: 500;
    margin: 8px 8px;
    text-align: center;
    display: block;
    background-color: #333333;
}

.gnm-weather-summary-small .button-radar{
  padding: 0px;
}

@media (max-width:400px) {
  .gnm-weather-summary-small h2{
    font-size: 47px;
    line-height: 75px;
  }
    .gnm-weather-summary-small img{
      max-width: 70px;
    }
}

@media (max-width:349px) {
  .gnm-weather-summary-small li{
    width: 100%;
    text-align: center;
  }
}
