.gnm-seven-day-forecast {
  margin-top: 15px;
  width: 100%;
  margin-bottom: 15px;
  overflow-x: scroll;
  overflow-y: hidden;
  padding-bottom: 17px;
  margin-bottom: -15px;
}

.gnm-seven-day-forecast {
  .day {
    float: left;
    width: 100px;
  }

  .summary {
    margin: 1px;
    background-color: #333333;
    height: 240px;
    text-align: center;
    position: relative;
  }

  .img {
    height: 57px;
    padding: 5px 25px;
  }

  .title {
    text-transform: uppercase;
  }

  h2 {
    color: white;
    font-size: 16px;
    font-weight: 500;
    padding: 12px 0;
  }

  h3 {
    color: white;
    font-size: 26px;
    font-weight: 700;
    padding: 8px 0;
  }

  h4 {
    color: white;
    font-size: 24px;
    font-weight: 500;
    padding: 8px 0;
  }

  h5 {
    color: #acbfe5;
    font-size: 18px;
    font-weight: 500;
    padding: 8px 0;
  }

  .full-info {
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% - 30px);
    margin: 0 15px;
    background-color: white;
    z-index: 2;
    cursor: pointer;
    border: 1px solid #707070;
    border-radius: 3px;
    overflow: hidden;
    -webkit-box-shadow: 0 2px 25px -5px rgba(112,112,112,1);
    -moz-box-shadow: 0 2px 25px -5px rgba(112,112,112,1);
    box-shadow: 0 2px 25px -5px rgba(112,112,112,1);
  }

  .full-info {
    div,
    li,
    p {
      color: black;
    }
  }

  .full-info p {
    margin-top: 20px;
    font-size: 14px;
  }

  .full-info .condition {
    font-size: 22px;
    font-weight: 500;
    margin-top: 25px;
  }

  .full-info .high {
    font-size: 34px;
    font-weight: 500;
    padding-right: 25px;
  }

  .full-info ul {
    display: inline-block;
    list-style: none;
    padding: 5px;
  }

  .full-info li:nth-child(1) {
    color: #666666;
    font-size: 12px;
    line-height: 12px;
  }

  .full-info li:nth-child(2) {}

  .full-info .full-title {
    background-color: black;
    color: white;
    padding: 3px;
    font-weight: 500;
  }

  .full-info .full-title span {
    float: right;
    margin: 4px;
  }

  .full-info .full-title span:hover:before {
    cursor: pointer;
  }
  @media (min-width: 996px) {
    .day.kotv {
      width: 14%;
    }

    .day.kwtv {
      width: 11%;
    }

    .summary {
      border-right: 1px solid #999999;
      background-color: #fbfbfb;
      margin: 0;
      height: 295px;
      cursor: pointer;
    }

    .summary:hover {
      background-color: #fbfbfb;
    }

    .condition-description {
      height: 50px;
      text-align: center;
      font-size: 14px;
      color: #333333;
      padding: 8px;
    }

    h2 {
      background-color: black;
      padding: 2px 0;
    }

    h2.Saturday {
      background-color: #ffc233;
      color: black;
    }

    h2.Sunday {
      background-color: #be0000;
    }

    h3 {
      color: black;
      font-weight: 500;
    }

    h4 {
      color: #999999;
      font-weight: 400;
    }

    h5 {
      color: #0f3d99;
      font-weight: 400;
    }

    .details {
      position: absolute;
      bottom: 0;
      width: 100%;
      text-align: center;
      font-size: 12px;
    }

    .arrow-right {
      position: absolute;
      bottom: -1px;
      right: -5px;
      width: 16px;
      height: 8px;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-bottom: 8px solid #999999;
      transform: rotate(135deg);
    }

    .summary:hover .arrow-right {
      border-bottom-color: black;
    }
  }
}
