//
// Scaffolding
// --------------------------------------------------

@-webkit-viewport { width:extend-to-zoom 100%; zoom:1; orientation:auto; min-zoom:1; initial-scale:auto; }
   @-moz-viewport { width:extend-to-zoom 100%; zoom:1; orientation:auto; min-zoom:1; initial-scale:auto; }
    @-ms-viewport { width:extend-to-zoom 100%; zoom:1; orientation:auto; min-zoom:1; initial-scale:auto; }
     @-o-viewport { width:extend-to-zoom 100%; zoom:1; orientation:auto; min-zoom:1; initial-scale:auto; }
        @viewport { width:extend-to-zoom 100%; zoom:1; orientation:auto; min-zoom:1; initial-scale:auto; }
        @viewport { width:device-width; user-zoom:zoom; }

// Reset the box-sizing
//
// Heads up! This reset may cause conflicts with some third-party widgets.
// For recommendations on resolving such conflicts, see
// http://getbootstrap.com/getting-started/#third-box-sizing
* {
  @include box-sizing(border-box);
}
*:before,
*:after, .noselect{
  @include box-sizing(border-box);

  -webkit-touch-callout:none;
  -webkit-user-select:none;
  -khtml-user-select:none;
  -moz-user-select:-moz-none;
   -moz-user-select:none;
   -ms-user-select:none;
   user-select:none;
   cursor:default;
   -webkit-tap-highlight-color:rgba(0,0,0,0);
    -webkit-tap-highlight-color:transparent;
}

html, body {
 min-height:100%;
 width: 100%;
 scroll-behavior:smooth;
}

body, body *, body *:before, body *:after {
  margin:0;
  padding:0;
  box-sizing:inherit;
}


// Body reset

html {
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  height:100%;
  width:100%;
  overflow-y:scroll;
  margin:0 auto;
  padding:0;

   -ms-touch-action:manipulation;
    touch-action:manipulation;
}

body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  line-height: $line-height-base;
  color: $text-color;
  background-color: $body-bg;
  font-style:normal;
  font-kerning:normal;
  font-weight:400;
  text-align:left;
  overflow:hidden;
  overflow-wrap: break-word;
  word-wrap: break-word;
  position:relative;

  text-rendering:optimizeLegibility;
  -webkit-text-size-adjust:100%;
  -moz-osx-font-smoothing:grayscale;
  -webkit-font-smoothing:antialiased;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
}

// Reset fonts for relevant elements
input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}


// Links

a {
  color: $link-color;
  text-decoration: none;

  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
  }

  &:focus {
    @include tab-focus;
  }
}


// Figures
//
// We reset this here because previously Normalize had no `figure` margins. This
// ensures we don't break anyone's use of the element.

figure {
  margin: 0;
}


// Images

img {
  vertical-align: middle;
}

// Responsive images (ensure images don't scale beyond their parents)
.img-responsive {
  @include img-responsive;
}

// Rounded corners
.img-rounded {
  border-radius: $border-radius-large;
}

// Image thumbnails
//
// Heads up! This is mixin-ed into thumbnails.less for `.thumbnail`.
.img-thumbnail {
  padding: $thumbnail-padding;
  line-height: $line-height-base;
  background-color: $thumbnail-bg;
  border: 1px solid $thumbnail-border;
  border-radius: $thumbnail-border-radius;
  @include transition(all .2s ease-in-out);

  // Keep them at most 100% wide
  @include img-responsive(inline-block);
}

// Perfect circle
.img-circle {
  border-radius: 50%; // set radius in percents
}


// Horizontal rules

hr {
  margin-top:    $line-height-computed;
  margin-bottom: $line-height-computed;
  border: 0;
  border-top: 1px solid $hr-border;
}


// Only display content to screen readers
//
// See: http://a11yproject.com/posts/how-to-hide-content

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}

// Use in conjunction with .sr-only to only display content when it's focused.
// Useful for "Skip to main content" links; see http://www.w3.org/TR/2013/NOTE-WCAG20-TECHS-20130905/G1
// Credit: HTML5 Boilerplate

.sr-only-focusable {
  &:active,
  &:focus {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    clip: auto;
  }
}


// iOS "clickable elements" fix for role="button"
//
// Fixes "clickability" issue (and more generally, the firing of events such as focus as well)
// for traditionally non-focusable elements with role="button"
// see https://developer.mozilla.org/en-US/docs/Web/Events/click#Safari_Mobile

[role="button"] {
  cursor: pointer;
}
