.gnm-weather-app-advert {
  background-color: #222;
  padding-top: 20px;
  padding-bottom:20px;
}

.gnm-weather-app-advert h3 {
  color: #e5e5e5;
  font-size: 24px;
  padding-bottom:10px;
}

.gnm-weather-app-advert h4 {
  font-weight:600;
  color: #e5e5e5;
  font-size:20px;
}

.gnm-weather-app-advert p {
  font-size: 16px;
  line-height: 25px;
  margin: 20px 0 20px;
  font-weight: 400;
  color: #e5e5e5;
}

.gnm-weather-app-advert .apps {
  color: #e5e5e5;
  line-height: 40px;
  margin-top:20px;
}

.gnm-weather-app-advert .ios {
  display: block;
  float: left;
}

.gnm-weather-app-advert .android {
  display: block;
  float: left;
  margin: 0 10px;
}

.gnm-weather-app-advert .google {
  height: 40px;
}
