/* This HAS to stay CSS-only for legacy support of consumer-affairs */

.gnm-video-player{
  position: relative;
}

.gnm-video-player .video-js{
  font-size: 15px;
}

.gnm-video-player.popup{
  position: fixed;
  top:175px;
  left: 10vw;
  width: 50vw;
  z-index: 31;
}
@media (max-width: 996px) {
  .gnm-video-player.popup{
    width: 70vw;
  }
}

@media (max-width: 768px) {
  .gnm-video-player.popup{
    width: calc(100vw - 30px);
    top:150px;
    left: 5px;
  }
}
.gnm-video-player button.close-button{
  display: none;
}

.gnm-video-player.popup button.close-button{
  display: block;
  position: absolute;
  top: 0px;
  right: 0px;
  cursor: pointer;
  transition: background-color 200ms ease-in-out;
  background-color: rgba(0,0,0,0.5);
  border: none;
  z-index: 4;
}
.gnm-video-player button.close-button:hover{
  background-color: rgba(0,0,0,1)
}

.gnm-video-player button.close-button > span{
  color: white;
  font-size: 20px;
  padding:4px;
  cursor: pointer;
}

.gnm-video-player .video-js .vjs-big-play-button{
  width: 70px;
  height:70px;
  border-radius: 40px;
  top: calc(50% - 35px);
    left: calc(50% - 35px);
}
