.gnm-top-stories {

  margin-right: 0;
  margin-bottom: 2rem;
}

.gnm-top-stories img{
  width:100%;
}

.gnm-top-stories [class*="col-"] {
  padding: 0;
  border: 1px solid #fff;
  line-height: 0.8

}

.gnm-top-stories .gradient-inline-block {
  display: block;
}

.gnm-top-stories .gradient-inline-block, .gradient-block {
  position: relative;
  display: inline-block;
  transition: all .5s ease-out;
  text-decoration: none;
}

.gnm-top-stories .gradient-inline-block .media-time, .gradient-block .media-time {
  color: #ccc;
  display: block;
}

.gnm-top-stories .media-time{
  font-size: 1.5rem;
  text-shadow: 1px 4px 9px black;
}

.gnm-top-stories .media-title{
  margin-bottom: 3px;
  font-size: 2rem;
  font-weight: 500;
  text-shadow: 1px 4px 9px black;

}


.gnm-top-stories .ie-spacer{
  width:100%;
  height: 1px;
}

.gnm-top-stories .category-flag{

  display: inline-block;

  padding: 4px 5px;
  background-color: #be0000;
  font-size: 1.4rem;
  font-weight: 500;
  color: white;

}

.gnm-top-stories .category-flag.news{
  background-color: #be0000;
}
.gnm-top-stories .category-flag.weather{
  background-color: #de3a00;
}
.gnm-top-stories .category-flag.sports{
  background-color: #006eff;
}
.gnm-top-stories .category-flag.lifestyle{
  background-color: #9400be;
}
.gnm-top-stories .category-flag.recipes{
  background-color:  #00892e;
}


.gnm-top-stories .gradient-inline-block:before {
  content: " ";
  position: absolute;
  left: 0;
  height: 100%;
  width: 100%;

  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, 0)), color-stop(50%, rgba(0, 0, 0, .5)), color-stop(100%, rgba(0, 0, 0, 1)));
  background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .5) 50%, rgba(0, 0, 0, 1) 100%);
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .5) 50%, rgba(0, 0, 0, 1) 100%);
  transition: opacity .25s ease-out;
  opacity: 1;
}

.gnm-top-stories .gradient-inline-block__headline {
  position: absolute;
  bottom: 1rem;
  padding: 0 10px;
  color: #fff;
  line-height: 1.3;
  min-width:200px; /* only to prevent the letters from scattering accross the page prior to paint */
  width: 100%;
}

.gnm-top-stories.top-story-block .gradient-inline-block {
  position: relative;
  display: block;
  width:100%;
}

/* === small devices (mobile) ==================================== */

  @media (min-width:1024px) {
    .gnm-top-stories .gradient-inline-block:hover:before {
      opacity: .75;
      transition: opacity .125s ease-out;
    }
  }
