/* This HAS to stay CSS-only for legacy support of consumer-affairs */

.gnm-footer {

  width: 100vw;
      margin-left: -16px;
      color: #999;
      background-color: black;
      font-size: 12px;
}

.gnm-footer *{
  line-height: 130%;
}

.gnm-footer .no-gutter > [class*='col-'] {
    padding-right:0;
    padding-left:0;
    /* don't know why, but this is needed for consumer affairs */
}


.gnm-footer .row {
  /* these are redundant, but frankly overrode them */
  margin-right : -15px;
  margin-left  : -15px;
}

.gnm-footer .major-column {
  margin-top    : 10px;
  margin-bottom : 10px;
  padding-left  : 25px;
}

.gnm-footer a {
  color           : #999;
  text-decoration : none;
}

.gnm-footer a.social-icons {
  display         : block;
  color           : #fff;
  text-decoration : none;
  font-size       : 26px;
  font-weight     : 30;
  transition      : all 300ms;
  cursor          : pointer;
}

.gnm-footer a.social-icons i::before {
  cursor : pointer;
}

.gnm-footer a.social-icons:hover {
  color : #f7cd6f;
}

.gnm-footer .sign-up-button {
  padding-right : 5px;
  padding-left  : 5px;
}

.gnm-footer .sign-up-button:hover {
  color            : #111111;
  background-color : lightgray;
  transition       : all 300ms;
  cursor           : pointer;
}

.gnm-footer a:hover {
  text-decoration : underline;
  transition      : all 0.3s;
  cursor          : pointer;
}

.gnm-footer a.primary-link {
  display         : block;
  margin-top      : 5px;
  color           : #fff;
  text-decoration : none;
  font-size       : 18px;
  font-weight     : 30;
  transition      : all 300ms;
}

@media (max-width:768px) {
  .gnm-footer a.primary-link{
    font-size: 16px;
  }
}

.gnm-footer a.primary-link:hover {
  text-decoration : underline;
  cursor          : pointer;
}

.gnm-footer .input-group {
  max-width : 400px;
  margin    : 0 auto 5px auto;
}

.gnm-footer .descriptive-text {
  padding-bottom : 6px;
  color          : #fff;
  font-size      : 12px;
  font-weight    : 500;
}

.gnm-footer .emphasis-text {
  color       : #fff;
  font-size   : 14px;
  font-weight : 350;
}

.gnm-footer .small-emphasis-text {
  color       : #fff;
  font-size   : 14px;
  font-weight : 350;
}

.gnm-footer .small-emphasis-text .col-xs-6 {
  padding-right : 0px;
}

.gnm-footer .centered {
  text-align : center;
}

.gnm-footer .map {
  display       : block;
  max-width     : 400px;
  margin        : 0 auto;
  padding-top   : 10px;
  padding-right : 0px;
  padding-left  : 15px;
}

.gnm-footer .griffin-details {
  padding-top: 10px;
  text-align  : center;
  line-height : 18px;
}

.gnm-footer .input-group{ /* temporary until we get some real info */
  display: none;
}
