.gnm-simple-temp-chart{
  padding-left: 5px;

  position: relative;
  overflow: hidden;
}

.gnm-simple-temp-chart .chart{
  position: absolute;
  bottom: 20px;
  width: 100%;
}

.gnm-simple-temp-chart .bar{
  display: inline-block;
  position: relative;
}

.gnm-simple-temp-chart .bar .label{
  color: black;
      position: absolute;
      top: -12px;
      font-size: 10px;
      width: 100%;
      text-align: center;
      z-index: 1;
      /* left: -6px; */
      padding-left: 0px;

}

.gnm-simple-temp-chart .legend{
  position: absolute;
    width: calc(100% - 1px);
    height:  28px;
    bottom: -20px;
    background-color: #333333;
}

.gnm-simple-temp-chart .bar .timelabel{
  color:white;
  position: absolute;
  bottom:-20px;
  z-index: 1;
  font-size: 14px;
  left:-15px;
}

.gnm-simple-temp-chart .bar .precipitation{
  color:black;
  position: absolute;
  top:20px;
  font-weight: 500;
  z-index: 1;
  font-size: 12px;

}



.gnm-simple-temp-chart .bar .windvector{
  position: absolute;
  color:white;
  bottom:0px;
  width: 100%;
  text-align: center;
  z-index: 1;
  left: -3px;
  font-size: 10px;
}
